const GITHUB_API_URL = 'api.github.com';
const GITHUB_URL = 'github.com';

function mapApiToDomainUrl(url) {
  return url.replace(`${GITHUB_API_URL}/repos`, GITHUB_URL);
}

function mapCommitUrl(commit) {
  const url = mapApiToDomainUrl(commit.url).replace('commits', 'commit');

  return {
    ...commit,
    url,
  };
}

function mapRepoUrl(repo) {
  const url = mapApiToDomainUrl(repo.url);

  return {
    ...repo,
    url,
  };
}

export function getLatestCommit(events) {
  const pushEvents = events.filter(({ type }) => type === 'PushEvent');
  const commit = mapCommitUrl(pushEvents[0].payload.commits[0]);
  const repo = mapRepoUrl(pushEvents[0].repo);

  return {
    commit,
    repo,
  };
}
