/**
 * Layout component that queries for data
 * with Gatsby's StaticQuery component
 *
 * See: https://www.gatsbyjs.org/docs/static-query/
 */

import React, { useState } from 'react';
import PropTypes from 'prop-types';
import styled, { ThemeProvider, css } from 'styled-components';
import Helmet from 'react-helmet';

import Footer from '../Footer';
import Latest from '../Latest';
import Header from '../Header';
import { GlobalStyles, themes, styleHelpers } from '../../styles';
import * as GithubService from '../../services/github';
import * as githubApi from '../../api/github';
import Fetcher from '../Fetcher/Fetcher';

const contentStyles = ({ theme, location }) => css`
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin: 0 30px;
  max-width: 960px;
  min-height: 52vh;
  background-color: ${theme.colors.p000};
  flex-grow: 1;

  ${styleHelpers.media.medium`
    margin: 0 50px;
  `};

  ${styleHelpers.media.large`
    margin: 0 auto;
  `};
`;

const contentHomeStyles = ({ location }) =>
  location.pathname === '/' &&
  css`
    max-width: 100%;
    margin: 0;
  `;

const Content = styled('div')`
  ${contentStyles}${contentHomeStyles}
`;

function Layout({ children, location, theme }) {
  const [isDark, setDark] = useState(false);
  const { darkTheme, mainTheme } = themes;

  return (
    <ThemeProvider theme={isDark ? darkTheme : mainTheme}>
      <>
        <Helmet
          meta={[
            {
              name: 'theme-color',
              content: isDark ? darkTheme.colors.p000 : mainTheme.colors.p200,
            },
          ]}
        />
        <GlobalStyles />
        <Header
          location={location}
          onModeSwitch={checked => setDark(checked)}
          isDark={isDark}
        />
        <Content location={location}>{children}</Content>
        <Footer>
          <Fetcher action={githubApi.getUserEvents('GuyLivni')}>
            {events => {
              const { commit, repo } = GithubService.getLatestCommit(events);
              return <Latest commit={commit} repo={repo} />;
            }}
          </Fetcher>
        </Footer>
      </>
    </ThemeProvider>
  );
}

Layout.propTypes = {
  children: PropTypes.node.isRequired,
  location: PropTypes.object.isRequired,
};

export default Layout;
